.dialogs {
    display: flex;
    flex-direction: column;
    color: #000;
}
.dialog {
    border-bottom: 1px solid rgb(235, 235, 235);
    margin-bottom: 20px;
    display: flex;
}
.messages textarea {
    width: 100%;
    height: 100px;
    margin: 0 auto;
    border-radius: 10px;
    border: 1px solid rgb(45, 80, 165);
    padding: 10px;
    outline: none;
    resize: none;
}
.btnLineForm{
    display: flex;
    justify-content: space-between;
}